<template>
    <div class="details">
        <van-nav-bar :title="item.ProdName" left-arrow @click-left="handleToShop" :fixed='true'>
            <!-- <van-icon name="wap-nav" slot="right" /> -->
        </van-nav-bar>
        <div class="banner">
            <img :src="imgSrc"/>
    </div>
            <div class="content" v-if="item.ProdName">
                <h3 class="content-header">{{item.ProdName}}</h3>
                <ul class="content-box">
                    <!-- <li class="box-item">
        <span>兑换方式: {{item.Intro}}</span>
    </li> -->
                    <li class="box-item">
                        <span>消耗积分:</span>
                        <span class="box-brd" v-if="item.ExchangeIntegral!==item.DiscountIntegral">
                            <span class="delete-int">{{item.ExchangeIntegral}}</span>
                        </span>
                        <span class="box-int">{{item.DiscountIntegral}}</span>
                    </li>
                    <li class="box-item">
                        <span>库存数量: {{item.Stock}}</span>
                    </li>
                    <li class="box-item">
                        <span>有效期: {{`${item.CreateDate} 至 ${item.EndDate} 23:59:59`}}</span>
                    </li>
                </ul>
            </div>
            <div class="footer">
                <div class="foot-box">
                    <h5 class="footer-title">奖品介绍</h5>
                    <div class="item-intro">{{item.Intro}}</div>
                </div>
            </div>
            <div class="footer-btn" @click="exchange" ref="footerBtn">
                兑换
            </div>
        </div>
</template>

<script type="text/ecmascript-6">
import Header from "../../common/header/header.vue";
import { mapGetters, mapMutations } from "vuex";
import { imgPath } from "../../common/dayuan";

export default {
    name: "Details",
    data() {
        return {
            item: {},
            shopStatus: false
        };
    },
    computed: {
        ...mapGetters(["DETAILS_ID", "ADDRESS_ID", "INTEGRAL"]),
        imgSrc() {
            let imgsrc = "";
            for (let i in this.item) {
                if (i === "ProdPicList") {
                    this.item[i].forEach(val => {
                        if (val.FileType !== 2) {
                            return;
                        } else {
                            imgsrc = imgPath() + val.FilePath + "&w=640&h=265";
                        }
                    });
                }
            }
            return imgsrc;
        }
    },
    components: {
        Header
    },
    mounted() {
        this.handleGetInfo();
    },
    methods: {
        ...mapMutations({ changeGoodIntegral: "CHANGE_GOODINTEGRAL" }),
        handleDateReleplace(timer) {
            let time = timer;
            time = time.replace(/-/g, "/");
            time = time.replace(/(\.\d+)?/g, "");
            return new Date(time);
        },
        handleGetInfo() {
            if (this.DETAILS_ID !== 0) {
                this.$toast.loading({
                    mask: true,
                    message: "加载中..."
                });
                this.Axios.get(
                    `/api/v1/Reg/ExchangeProdInfoGet?exchangeProdId=${
                        this.DETAILS_ID
                    }`
                )
                    .then(res => {
                        this.item = JSON.parse(JSON.stringify(res.data.Data));
                        const data = res.data.Data;
                        let startTime = this.handleDateReleplace(
                            data.CreateDate
                        );
                        const end_data = `${data.EndDate} 23:59:59`;
                        let endTime = this.handleDateReleplace(end_data);
                        let newDate = new Date();
                        console.log(endTime);
                        console.log(new Date());
                        if (
                            startTime > endTime ||
                            data.DiscountIntegral > data.ExchangeIntegral ||
                            data.Stock <= 0 ||
                            newDate > endTime
                        ) {
                            this.shopStatus = true;
                            this.$refs["footerBtn"].style.backgroundColor =
                                "gray";
                        }
                        setTimeout(() => {
                            this.$toast.clear();
                        }, 200);
                    })
                    .catch(err => {
                        this.$toast.clear();
                        this.$toast("网络出现了问题");
                    });
            }
        },
        exchange() {
            if (this.shopStatus === true) {
                return false;
            } else {
                if (this.INTEGRAL < this.item.DiscountIntegral) {
                    this.$toast("积分不足，无法兑换");
                    return;
                }
                this.changeGoodIntegral(this.item.DiscountIntegral);
                this.$router.push({ path: "/mine" });
            }
        },
        handleToShop() {
            this.$router.back();
        }
    }
};
</script>

