<template>
    <div class="header">
        <div class="left">
            <van-icon v-if="leftIcon" name="close" @click="handleToPages" />
        </div>
        <div class="content">{{this.title}}</div>
        <div class="right">
            <van-icon name="more-o" />
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {};
  },
  props: {
    title: {
      type: [String, Number],
      default: "..."
    },
    page: {
      type: String
    },
    leftIcon: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    handleToPages() {
      this.$router.push({ path: this.page });
    }
  }
};
</script>

